<template>
  <div class="common-info">
    <div class="topTitle">
      <span class="topTitle-span1">统计分析</span>
      <span class="topTitle-span1">
        <img src="@/assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">客户评级信息统计</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
          <div class="export-content">
            <Button type="primary"  @click="exportFn" v-authcontrol="{menuCode:'109003001000'}">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
          </div>
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>查询条件
            <div class="button-content">
            </div>
          </div>
          <div>
            <Form
              ref="formValidate"
              :model="formValidate"
              :rules="ruleValidate"
              :label-width="120"
              inline
            >
              <Row>
                <FormItem label="统计日期：" prop="date1">
                    <DatePicker split-panels type="daterange" :value="formValidate.date1" :options="options1" @on-change="formValidate.date1=$event"></DatePicker>
                </FormItem>
              </Row>
              <Row>
                <div class="btnClass">
                  <Button type="primary" @click="handleSubmit()" style="width:89px">查询</Button>
                </div>
              </Row>
            </Form>
          </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>客户身份识别及风险等级统计
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox2" border="1" cellpadding="2">
              <thead>
                <tr align="center">
                  <td colspan="2">项目</td>
                  <td rowspan="1">自然人客户</td>
                  <td rowspan="1">机构客户</td>
                </tr>
              </thead>
              <tbody>
                    <tr>
                        <td :rowspan="3">客户身份识别</td>
                        <td>
                            初次识别客户数（含一次性业务）
                        </td>
                        <td>{{statCraInfo?statCraInfo.addCntPer:""}}</td>
                        <td>{{statCraInfo?statCraInfo.addCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            重新或持续识别客户数
                        </td>
                        <td>{{statCraInfo?statCraInfo.contiCntPer:""}}</td>
                        <td>{{statCraInfo?statCraInfo.contiCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            中止服务客户数
                        </td>
                        <td>{{statCraInfo?statCraInfo.deletCntPer:""}}</td>
                        <td>{{statCraInfo?statCraInfo.deletCntOrg:""}}</td>
                    </tr>



                     <tr>
                        <td :rowspan="15">客户风险等级分类</td>
                        <td>
                            I类客户数（截止评估期末）
                        </td>
                        <td>{{statCraInfo?statCraRank[4].allCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[4].allCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：评估期调整数
                        </td>
                        <td>{{statCraInfo?statCraRank[4].adjCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[4].adjCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：限制功能客户数
                        </td>
                        <td>{{statCraInfo?statCraRank[4].limitCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[4].limitCntOrg:""}}</td>
                    </tr>


                    <tr>
                        <td>
                            II类客户数（截止评估期末）
                        </td>
                        <td>{{statCraInfo?statCraRank[3].allCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[3].allCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：评估期调整数
                        </td>
                        <td>{{statCraInfo?statCraRank[3].adjCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[3].adjCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：限制功能客户数
                        </td>
                        <td>{{statCraInfo?statCraRank[3].limitCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[3].limitCntOrg:""}}</td>
                    </tr>


                    <tr>
                        <td>
                            III类客户数（截止评估期末）
                        </td>
                        <td>{{statCraInfo?statCraRank[2].allCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[2].allCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：评估期调整数
                        </td>
                        <td>{{statCraInfo?statCraRank[2].adjCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[2].adjCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：限制功能客户数
                        </td>
                        <td>{{statCraInfo?statCraRank[2].limitCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[2].limitCntOrg:""}}</td>
                    </tr>


                    <tr>
                        <td>
                            IV类客户数（截止评估期末）
                        </td>
                        <td>{{statCraInfo?statCraRank[1].allCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[1].allCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：评估期调整数
                        </td>
                        <td>{{statCraInfo?statCraRank[1].adjCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[1].adjCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：限制功能客户数
                        </td>
                        <td>{{statCraInfo?statCraRank[1].limitCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[1].limitCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            V类客户数（截止评估期末）
                        </td>
                        <td>{{statCraInfo?statCraRank[0].allCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[0].allCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：评估期调整数
                        </td>
                        <td>{{statCraInfo?statCraRank[0].adjCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[0].adjCntOrg:""}}</td>
                    </tr>
                    <tr>
                        <td>
                            其中：限制功能客户数
                        </td>
                        <td>{{statCraInfo?statCraRank[0].limitCntPer:""}}</td>
                        <td>{{statCraInfo?statCraRank[0].limitCntOrg:""}}</td>
                    </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
      this.formValidate.date1 = [this.getFormatDate(), this.getFormatDate()];
  },
  data() {
    return {
      statCraInfo: null,
      statCraRank: [],
      formValidate: {
        date1: ["",""]
      },
      tableBody: [
      ],
      options1: {
          disabledDate (date) {
              return date && date.valueOf() > Date.now()-24*60*60*1000;
          }
      },
       ruleValidate: {
          date1: [
            {
              required: true,
              validator: (rule, value, callback) => {
                if (this.formValidate.date1[0] == "") {
                  return callback(new Error("请选择时间"));
                } else {
                  callback();
                }
              },
              trigger: "on-change"
            }
        ],
       }
    };
  },
  mounted() {},
  methods: {
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn(){
        if (!this.statCraInfo) {
            this.$Message.warning("暂无数据");
            return;
        }
        let data = {
          start: this.formValidate.date1[0],
          end: this.formValidate.date1[1]
        };
        data = JSON.stringify(data);
        this.axios
          .post("/stat/cra/doExportCraStatInfo", data, {
            headers: { "Content-Type": "application/json" },
            responseType: "blob"
          })
          .then(response => {
              this.download(response,'导出文档.xls');
          })
          .catch(error => {});
    },
    getFormatDate() {
        var date = new Date(new Date()-24*60*60*1000);
        var seperator1 = "-";
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentdate = year + seperator1 + month + seperator1 + strDate;
        return currentdate;
    },
    handleSubmit(){
      let valid1 = "";
      this.$refs["formValidate"].validate(valid => {
          valid1 = valid;
      });
      if (!valid1)  return;
        let data = {
          start: this.formValidate.date1[0],
          end: this.formValidate.date1[1]
        };
        data = JSON.stringify(data);
        this.axios
          .post("/stat/cra/getCraStatInfo", data, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code=="200"){
                  if (!response.data.statCraInfo){
                    this.$Message.info("请稍后尝试查询，正在执行中");
                  }
                  this.statCraInfo = response.data.statCraInfo;
                  this.statCraRank = response.data.statCraRank;
            }
          })
          .catch(error => {});
    },
  },
  watch: {}
};
</script>
<style lang="scss" @scoped>
.tableBox2{
  tbody{
    td{
      border: 1px solid #dcdee2;
    }
  }
}
</style>


